
 .weedDoctorBody{
    height: auto;
    display: grid;
    margin-top: 10%;
    grid-template-rows: 33vh 18vh 4vh 106vh 60vh 100vh;
   
  }
  @media screen and (max-width: 730px),(orientation: portrait)  {
    .weedDoctorBody{display: block; height: 100%;}
    .wdsectionTwo, .wdsectionThree , .wdsectionFour, .wdsectionFive{
        height: 0%;
    }
  }


.wdsectionTwo {
    display: grid;
    justify-items: center;
    
    background-color:black;
    height: 100%;
}

.wdsectionThree {
    background-color:black;
    height: 100%;
}

.wdsectionFour {
    background-color:black;
    height: 100%;
    margin-top: 10%;
    
}

.wdsectionFive {
   
    height: 100%;
}


.wdOverviewImg{
    width: 100%;
}

.wdFlowchartImg{
    width: 100%;
}

.wdFigmaImg{
    width: 100%;
}