
.somzoaBody{
    width: 91vh;
    height: auto;
    display: grid;
    margin-top: 10%;
    grid-template-rows: 15vh 16vh 5vh 310vh 5vh ;
    
    grid-template-columns: 91vh;
  }
  

.somzoaProjects{
    height: auto;
    display: grid;
    gap: 30vh;
    grid-template-columns: 1fr;
    grid-template-rows: 35vh 120vh 60vh;
}
    

.szSectionTwo {
    display: grid;
    justify-items: center;
    
    padding-top: 1vh;
    height: 100%;
    width: 100%;
}

.szSectionThree {
    
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    height: 100%;
    width: 100%;
    justify-content: space-around;
}

.szSectionFour {
    display: grid;
    justify-items: center;
    width: 100%;
    height: 100%;
}

.szSectionFive {
    
    height: 100%;
}


